<template>
    <section>
        <div>
            <!-- HEADER CARD -->

            <div class="heading-border heading-cust pt-2 mb-3">
                     <h5 class="h5">
                          <span>Electricity Usage History</span>
                          <small class="float-right">
                               <a href="javascript:void(0)" @click="BackToList()" title=""  data-toggle-tooltip="tooltip" data-original-title="Back to List">
                                   <em class="fa fa-angle-double-left pr-1 text-dark"></em>{{ $t('BacktoList') }}
                               </a>
                          </small>
                     </h5>
            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 cl_us_history">
                <!-- <h2>Electricity Usage History</h2> -->
                <div class="sub pb-4"><span class="font-weight-400">Meter:</span> {{ meterName }}</div>

                <div class="row">
                    <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                        <div class="stat-item">
                            Average Consumption
                            <div class="stat-value">{{ AverageCousumption }} kWh</div>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                       <div class="stat-item">
                           Total Consumption
                           <div class="stat-value">{{ TotalCousumption }} kWh</div>
                       </div>
                    </div>

                    <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4"> 
                          <div class="stat-item">
                              Total Charges
                              <div class="stat-value"> {{ $n(TotalAmount, 'currency', $getLocaleByCurrency(Currency)) }}</div>
                          </div>
                    </div> 


                </div>
            </div>

            <!-- READING RECORDS -->
          
                <tg-list ModuleName="Client" SubModuleCode="Billing" IdentityColumn="utility_id" :listType="['List']"
                    :ListData="UtilityUsageData" :HeaderData="Headers"  :isShowHeaderText="false" :HeaderText="$t('ViewHistory')" :TotalRecords="TotalRecords" :IsShowAction="false"
                    :RenderRowActionMethod="() => { }" :ListDataCallBackFunction="ViewHistory"  
        
                    :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                    v-on:SortdataButtonClick="sortdata">
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'bill_net_total'">
                            <span>
                             {{ $n(data.row.bill_net_total, 'currency', $getLocaleByCurrency(Currency)) }}
                            </span>
                        </template> 
                    </template>
                </tg-list>
                <!-- <table>
                    <thead>
                        <tr>
                            <th>Date</th>
                            <th>Reading (kWh)</th>
                            <th>Consumption (kWh)</th>
                            <th>Charge</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="itm in UtilityUsageData" :key="itm.RowNum">
                            <td>{{ itm.reading_date }}</td>
                            <td>{{ itm.current_reading }}</td>
                            <td><span class="badge">{{ itm.consumption }}</span></td>
                            <td>${{ itm.bill_net_total }}</td>
                        </tr>

                        <tr v-if="UtilityUsageData.length === 0">
                            <td colspan="4" class="text-center">No records found</td>
                        </tr>
                    </tbody>
                </table> -->
          

        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    data() {
        return {
            UtilityUsageData: [],
            TotalAmount: null,
            TotalCousumption: null,
            AverageCousumption: null,
            Headers: [
                {
                    "COLUMN_NAME": "reading_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Date",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "current_reading",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Reading (kWh)",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "consumption",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Consumption (kWh)",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "bill_net_total",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Charge",
                    "DISPLAY_ORDER": 4,
                    settings : 
                    {
                        isInSlot: true,
                    }
                }
            ],
            TotalRecords: 0,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            CurrentPage: 1,
            TotalPages: 0,
            meterName:''
        };
    },
    created() {
        this.Currency = this.GetUserInfo.Currency.replace(/[()]/g, '');
        this.ViewHistory('Electric');
        this.client_id=this.$route.params.clientId;
        this.parentId=this.$route.params.parentId;
    },
    methods: {
        ViewHistory(UtilityName) {
            var vm = this;
            var User_Client_id= this.$route.params.clientId ? this.$route.params.clientId  : this.GetUserInfo.ID;
            const url = `sortBy=${this.SortBy}&sortExp=${this.SortExp}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&clientId=${User_Client_id}&relatedInfoId=${this.$route.params.id}&utilityName=${UtilityName}`;
            DataService.GetUtilityUsageData(url).then((response) => {
                if (response.data) {
                    debugger;
                    let UsageData = JSON.parse(response.data.result);
                    vm.UtilityUsageData = UsageData.DATA;
                    vm.TotalAmount = vm.UtilityUsageData[0].TotalAmount;
                    vm.TotalCousumption = vm.UtilityUsageData[0].TotalConsumption;
                    vm.AverageCousumption = vm.UtilityUsageData[0].AvgConsumption;
                    vm.meterName = vm.UtilityUsageData[0].unique_name;
                    this.TotalRecords = vm.UtilityUsageData[0].TOTAL_RECORDS;
                    this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                    this.CurrentPage = this.PageNumber;

                }
                else {
                    vm.UtilityUsageData = [];
                    vm.TotalAmount = '';
                    vm.TotalCousumption = '';
                    vm.AverageCousumption = '';
                    this.TotalRecords = 0;
                }

            });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.ViewHistory();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.ViewHistory();
        },
        BackToList() {
            var vm = this;
            debugger;
            if(vm.client_id>0){
                this.$router.push({name: "UpdateClient",params: { id: vm.client_id,parentId: vm.parentId },query: {redirectFrom: "Wallet" }});
            }else{
                vm.$router.push({ name: 'WalletRecharge' });
            }
            // vm.$router.push({ name: 'WalletRecharge' });

        }
    }
};
</script>
